@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Abel&display=swap");

.service {
  position: relative;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' version='1.1' xmlns:xlink='http://www.w3.org/1999/xlink' xmlns:svgjs='http://svgjs.dev/svgjs' width='1440' height='560' preserveAspectRatio='none' viewBox='0 0 1440 560'%3e%3cg mask='url(%26quot%3b%23SvgjsMask1005%26quot%3b)' fill='none'%3e%3crect width='1440' height='560' x='0' y='0' fill='rgba(241%2c 241%2c 241%2c 1)'%3e%3c/rect%3e%3cpath d='M1440 0L998.48 0L1440 60.73z' fill='rgba(255%2c 255%2c 255%2c .1)'%3e%3c/path%3e%3cpath d='M998.48 0L1440 60.73L1440 91.21L701.2 0z' fill='rgba(255%2c 255%2c 255%2c .075)'%3e%3c/path%3e%3cpath d='M701.2 0L1440 91.21L1440 198.56L681.57 0z' fill='rgba(255%2c 255%2c 255%2c .05)'%3e%3c/path%3e%3cpath d='M681.57 0L1440 198.56L1440 237.35L278.05000000000007 0z' fill='rgba(255%2c 255%2c 255%2c .025)'%3e%3c/path%3e%3cpath d='M0 560L351.83 560L0 405.28z' fill='rgba(0%2c 0%2c 0%2c .1)'%3e%3c/path%3e%3cpath d='M0 405.28L351.83 560L779.9300000000001 560L0 380.72999999999996z' fill='rgba(0%2c 0%2c 0%2c .075)'%3e%3c/path%3e%3cpath d='M0 380.73L779.9300000000001 560L958.8800000000001 560L0 286.25z' fill='rgba(0%2c 0%2c 0%2c .05)'%3e%3c/path%3e%3cpath d='M0 286.25L958.8800000000001 560L1234.6100000000001 560L0 161.79000000000002z' fill='rgba(0%2c 0%2c 0%2c .025)'%3e%3c/path%3e%3c/g%3e%3cdefs%3e%3cmask id='SvgjsMask1005'%3e%3crect width='1440' height='560' fill='white'%3e%3c/rect%3e%3c/mask%3e%3c/defs%3e%3c/svg%3e");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: inherit;

  #service {
    gap: 10vh;
    // width: 100%;

    align-items: center;
    justify-content: center;
    height: 140vh;
  }

  .main-text {
    position: absolute;
    width: 100%;
    top: 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    h3 {
      font-weight: 400;
      text-transform: uppercase;
      font-size: 16px;
    }

    h1 {
      font-weight: 600;
      font-size: 48px;
      text-transform: uppercase;
      letter-spacing: 3px;
    }
  }

  .card-section {
    .service-card {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 60%;
      height: 65vh;
      background-position: center;
      background-size: cover;

      h1 {
        color: #ffffff;
        text-align: center;
        padding-top: 15px;
        font-size: 40px;
        letter-spacing: 4px;
        text-transform: uppercase;
      }
    }

    .card1 {
      top: 100%;
      background-color: #f1f0f0;
      overflow: hidden;
      box-shadow: 2px 2px 10px #646262, -2px -2px 20px #3406ef;
      background-image: url(../images/s1.jpg);
      background-position: top;
    }
    .card2 {
      top: 150%;
      background-color: #f1f0f0;
      overflow: hidden;
      box-shadow: 2px 2px 10px #646262, -2px -2px 20px #3f9bd1;
      background-image: url(../images/s2.jpg);
      background-position: top;
    }
    .card3 {
      top: 200%;
      background-color: #f1f0f0;
      overflow: hidden;
      box-shadow: 5px 5px 5px #646262, -2px -2px 20px #ebf0f0;
      background-image: url(../images/s3.jpg);
      background-position: top;
      h1 {
        color: #3c3939;
      }
    }
  }
}

@media screen and (max-width: 650px) {
  .service {
    .card-section {
      .service-card {
        width: 80%;
        height: 55vh;
        h1 {
          color: #ffffff;
          text-align: center;
          padding-top: 15px;
          font-size: 1.6rem;
          letter-spacing: 4px;
          text-transform: uppercase;
        }
      }

      .card1 {
        opacity: 0.6;
      }
      .card2 {
        opacity: 1;
      }
      .card3 {
        opacity: 0.8;
        h1 {
          color: #3c3939;
        }
      }
    }
  }
}
