.circular-menu-navigation {
  position: fixed;
  top: 1.5rem;
  right: 1.5rem;
  z-index: 100;
}

.circular-menu-navigation .floating-btn {
  display: block;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: hsl(217, 89%, 61%);
  box-shadow: 0 2px 5px 0 hsla(0, 0%, 0%, 0.26);
  color: hsl(0, 0%, 100%);
  font-size: 1.2rem;
  text-align: center;
  line-height: 3.5;
  cursor: pointer;
  outline: 0;
}

.circular-menu-navigation.active .floating-btn {
  box-shadow: inset 0 0 3px hsla(0, 0%, 0%, 0.3);
}

.circular-menu-navigation .floating-btn:active {
  box-shadow: 0 4px 8px 0 hsla(0, 0%, 0%, 0.4);
}

.circular-menu-navigation .floating-btn i {
  font-size: 1.3em;
  transition: transform 0.2s;
}

.circular-menu-navigation.active .floating-btn i {
  transform: rotate(45deg);
}

.circular-menu-navigation:after {
  display: block;
  content: " ";
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -2;
  background-color: hsl(217, 89%, 61%);
  transition: all 0.3s ease;
}

.circular-menu-navigation.active:after {
  transform: scale3d(5.5, 5.5, 1);
  transition-timing-function: cubic-bezier(0.68, 1.55, 0.265, 1);
}

.circular-menu-navigation .items-wrapper {
  padding: 0;
  margin: 0;
}

.circular-menu-navigation .menu-item {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  z-index: -1;
  display: block;
  text-decoration: none;
  color: hsl(0, 0%, 100%);
  font-size: 1.2rem;
  width: 3em;
  height: 3em;
  border-radius: 50%;
  text-align: center;
  line-height: 3;
  background-color: hsla(0, 0%, 0%, 0.1);
  transition: transform 0.3s ease, background 0.2s ease;
}

.circular-menu-navigation .menu-item:hover {
  background-color: hsla(0, 0%, 0%, 0.3);
}

.circular-menu-navigation.active .menu-item {
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.circular-menu-navigation.active .menu-item:nth-child(1) {
  transform: translate3d(-7em, -1em, 0);
}

.circular-menu-navigation.active .menu-item:nth-child(2) {
  transform: translate3d(-6.5em, 3.2em, 0);
}

.circular-menu-navigation.active .menu-item:nth-child(3) {
  transform: translate3d(-3.5em, 6.3em, 0);
}

.circular-menu-navigation.active .menu-item:nth-child(4) {
  transform: translate3d(1em, 7em, 0);
}

