  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;

  .bg-video {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -100;
    opacity: 0.4;
    overflow: hidden;

    &__content {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  .animated-svg {
    position: absolute;
    width: 50px;
    bottom: -2%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0.4;
  }

  .logo-box {
    padding-top: 50px;
    padding-left: 13%;
    display: flex;
    align-items: center;

    img {
      width: 80px;
      opacity: 0.6;
      filter: drop-shadow(0 0 0.75rem rgba(255, 255, 255, 0.855));
      animation: fadein 1.5s ease-in 0.1s;
    }

    span {
      font-size: 1.7rem;
      font-weight: 400;
      opacity: 0.8;
      letter-spacing: 2px;
      animation: fadein 1.5s ease-in 0.1s;
    }

    @keyframes fadein {
      0% {
        scale: 0.4;
        transform: translateY(-20px);
        opacity: 0;
      }
      100% {
        scale: 1;
        opacity: 1;
      }
    }
  }

  .container {
    width: 1440px;
    min-width: 1440px;
    margin: 0 auto;

    .hero-inner {
      display: flex;
      // align-items: center;

      justify-content: space-between;
      margin: 0 24px;
      height: 100vh;

      .hero-content {
        padding-top: 100px;
        width: 50%;

        .hero-content-inner {
          width: 420px;
          color: #323232;
          margin: 0 auto;
          padding-left: 5%;

          h1 {
            font-weight: 500;
            margin-bottom: 24px;

            .hero-content-line {
              margin: 0;
              height: 44px;
              overflow: hidden;
            }

            .hero-content-line-first {
              font-weight: 300;
              font-size: 1.6rem;
            }
            .hero-content-line-second {
              letter-spacing: 1px;
              font-weight: 500;
              font-size: 32px;
              color: rgb(176, 136, 3);
            }
            .hero-content-line-third {
              margin-top: 10px;
              font-weight: 300;
              font-size: 24px;
              color: #474646;
            }
          }
          p {
            font-size: 14px;
            line-height: 24px;
            font-weight: 300;
            padding-right: 48px;
            margin-bottom: 56px;
          }
          .btn-row {
            position: relative;
            &:before {
              position: absolute;
              content: "";
              width: 56px;
              height: 1px;
              background: #dfdfdf;
              top: 50%;
              left: -68px;
            }
            button {
              background: none;
              border: none;
              font-size: 12px;
              padding: 0;
              cursor: pointer;
              text-transform: uppercase;
              font-weight: 700;
              letter-spacing: 0.7px;
              display: flex;
              align-items: center;
              position: relative;
              &:focus {
                outline: none;
              }
              .arrow-icon {
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: center;
                background: #007fff;
                height: 48px;
                width: 48px;
                border-radius: 100px;
                margin-left: 16px;
                left: 64px;
                transform-origin: center center;
              }
            }
          }
        }
      }
      // .hero-images {
      //   width: 50%;
      //   height: 100vh;
      //   position: relative;
      //   .hero-images-inner {
      //     .hero-image {
      //       position: absolute;
      //       overflow: hidden;
      //       top: 20%;
      //       left: 0px;
      //       width: 52%;
      //       height: 50%;

      //       img {
      //         position: absolute;
      //         top: 0;
      //         left: 0;
      //         right: 0;
      //         bottom: 0;
      //         width: 90%;
      //       }
      //     }
      //   }
      // }

      .hero-images {
        width: 50%;
        height: 100vh;
        .hero-images-inner {
          .hero-image {
            overflow: hidden;
            top: 0;
            left: 0;
            width: 70%;
            opacity: 0.8;
          }
        }
      }
    }
  }
}

// media queries

@media screen and (max-width: 1200px) {
  .hero {
    visibility: hidden;
    width: 100vw;
    height: 100%;
    padding: 0 10%;

    .logo-box {
      padding-top: 30px;
      padding-left: 0%;
    }

    .container {
      width: 100%;
      min-width: 90%;
      margin: 0 auto;

      .hero-inner {
        margin: 0 24px;
        height: 100%;

        .hero-content {
          padding-top: 100px;
          width: 50%;

          .hero-content-inner {
            width: 400px;
            margin: 0 auto;
            padding-left: 0%;

            h1 {
              margin-bottom: 24px;

              .hero-content-line {
                margin: 0;
                height: 44px;
                overflow: visible;
              }

              .hero-content-line-first {
                font-size: 1.5rem;
              }
              .hero-content-line-second {
                letter-spacing: 0.1px;
                font-size: 1.9rem;
              }
              .hero-content-line-third {
                margin-top: 10px;
                font-size: 1.4rem;
              }
            }
            p {
              width: 80%;
              font-size: 0.9rem;
              line-height: 24px;
              font-weight: 300;
              padding-right: 8px;
              margin-bottom: 36px;
            }
          }
        }
        .hero-images {
          width: 80%;
          height: 100vh;
          .hero-images-inner {
            .hero-image {
              overflow: hidden;
              position: relative;
              margin-left: 50px;
              top: 100px;
              width: 80%;
              opacity: 0.8;
            }
          }
        }
      }
    }
  }
}



